import React from 'react'
import './Navbar.css'
import  { useState } from 'react';
import {Squash as Hamburger } from 'hamburger-react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  import 'bootstrap/dist/js/bootstrap.js';
function Navbar(){
    const [state, setstate] = useState("open nav")
    function reverse(){
        if (state=="close nav"){
            setstate("open nav")
        }else{
            setstate("close nav")
        }
    } 
    return (
        <nav class="navbar navbar-light navbar-expand-md navigation-clean-button">
        <div class="container"><a class="navbar-brand" href="#">Company Name</a><button data-bs-toggle="collapse" class="navbar-toggler" data-bs-target="#navcol-1"><span class="visually-hidden">Toggle navigation</span><span class="navbar-toggler-icon"></span></button>
            <div class="collapse navbar-collapse" id="navcol-1">
                <ul class="navbar-nav me-auto">
                    <Link to="/" class="nav-item"><a class="nav-link active">Home</a></Link>
                    <Link to="/blog" class="nav-item"><a class="nav-link active">Blog</a></Link>
                    <li class="nav-item dropdown"><a class="dropdown-toggle nav-link" aria-expanded="false" data-bs-toggle="dropdown" href="#">Dropdown </a>
                        <div class="dropdown-menu"><a class="dropdown-item" href="#"><Link to="members">Members</Link></a><a class="dropdown-item" href="#">Second Item</a><a class="dropdown-item" href="#">Third Item</a></div>
                    </li>
                </ul><span class="navbar-text actions"> <a class="login" href="#"> <Link to="/login">Log In</Link></a><a class="btn btn-light action-button" role="button" href="#"><Link to="/register">Sign Up</Link></a></span>
            </div>
        </div>
    </nav>
    )
}

export default Navbar
